.announcer {
  display: flex;
  flex-direction: row;
  flex: 1;
  gap: 0.2rem;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background-color: var(--color-lightpink);
}

.announcer-item {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  height: 4.5rem;
}

.announcer-message {
  font-size: 1.25rem;
  text-transform: capitalize;
}

@media (min-width: 576px) {
  .announcer-message {
    font-size: 1.5rem;
  }
}

@media (min-width: 768px) {
  .announcer-message {
    font-size: 1.75rem;
  }
}

@media (min-width: 992px) {
  .announcer-message {
    font-size: 2rem;
  }
}
